class Room {
  //  key key 
  fullKeys(room) {
    let keys = ['name', 'cover', 'description', 'lastMessage'];
    for (let key of keys) {
      if (!(key in room)) {
        room[key] = null;
      }
    }
    room.unread = room.unread || 0;
  }
}

export default Room;



